<template>
  <div class="container mt-5">
    <h2 class="mb-4">회원가입(개인)</h2>
    <form @submit.prevent="submitForm">
      <div class="mb-3 row">
        <label for="name" class="col-sm-3 col-form-label">이름:</label>
        <div class="col-sm-6">
          <input
            type="text"
            v-model="name"
            class="form-control"
            id="name"
            placeholder="이름을 입력하세요"
            required
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label for="email" class="col-sm-3 col-form-label"
          >이메일(아이디):</label
        >
        <div class="col-sm-6">
          <div class="input-group">
            <input
              type="email"
              v-model="email"
              class="form-control"
              id="email"
              placeholder="이메일을 입력하세요"
              required
            />
            <button
              type="button"
              class="btn btn-secondary"
              @click="sendVerificationCode"
            >
              이메일 인증
            </button>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label for="verificationCode" class="col-sm-3 col-form-label"
          >인증번호:</label
        >
        <div class="col-sm-6">
          <div class="input-group">
            <input
              type="text"
              v-model="eccode"
              class="form-control"
              id="verificationCode"
              placeholder="인증번호를 입력하세요"
              required
            />
            <button
              type="button"
              class="btn btn-secondary"
              @click="confirmVerificationCode"
            >
              인증번호 확인
            </button>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label for="password" class="col-sm-3 col-form-label">비밀번호:</label>
        <div class="col-sm-6">
          <input
            type="password"
            v-model="password"
            class="form-control"
            id="password"
            placeholder="비밀번호를 입력하세요"
            required
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label for="confirmPassword" class="col-sm-3 col-form-label"
          >비밀번호 확인:</label
        >
        <div class="col-sm-6">
          <input
            type="password"
            v-model="confirmPassword"
            class="form-control"
            id="confirmPassword"
            placeholder="비밀번호를 다시 입력하세요"
            required
          />
        </div>
      </div>
      <div class="mb-3 row">
        <label for="businessName" class="col-sm-3 col-form-label"
          >닉네임:</label
        >
        <div class="col-sm-6">
          <div class="input-group">
            <input
              type="text"
              v-model="businessName"
              class="form-control"
              id="businessName"
              placeholder="닉네임을 입력하세요"
              required
            />
            <button
              type="button"
              class="btn btn-secondary"
              @click="checkDuplicate"
            >
              중복확인
            </button>
          </div>
        </div>
      </div>
      <div class="mb-3 row">
        <label for="phoneNumber" class="col-sm-3 col-form-label">연락처:</label>
        <div class="col-sm-6">
          <input
            type="tel"
            v-model="phoneNumber"
            class="form-control"
            id="phoneNumber"
            placeholder="연락처를 입력하세요 (예시: 010-1234-5678)"
            pattern="[0-9]{3}-[0-9]{4}-[0-9]{4}"
            required
          />
        </div>
      </div>
      <div class="mb-3 row">
        <div class="col-sm-3"></div>
        <div class="col-sm-6">
          <button type="submit" class="btn btn-primary">가입하기</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
export default {
    
  data() {
    return {
      name: "",
      email: "",
      verificationCode: "",
      password: "",
      confirmPassword: "",
      businessName: "",
      phoneNumber: "",
    };
  },
  methods: {
    submitForm() {
      // 회원가입 양식 제출 기능 구현
    },
    sendVerificationCode() {
      // 이메일 인증 코드 발송 기능 구현
        axios.post("http://localhost:8888/sendVerificationEmail", {
            email: this.email,
        })
        .then(response => {
            // 서버로부터 받은 응답 처리
            alert("서버 응답: " + response.data);
            console.log("서버 응답:", response.data);
        })
        .catch(error => {
            console.error("서버 오류:", error);
            alert("서버 오류 발생!");
        });
    },
    confirmVerificationCode() {
      // 인증번호 확인 기능 구현
      axios.post("http://localhost:8888/confirmVerificationCode", {
          email: this.email,
          eccode: this.eccode,
      })
      .then(response => {
        // 서버로부터 받은 응답 처리
        alert("서버 응답: " + response.data);
        console.log("서버 응답:", response.data);
      })
      .catch(error => {
        console.error("서버 오류:", error);
        alert("서버 오류 발생!");
      })
    },
    checkDuplicate() {
      // 닉네임 중복 확인 기능 구현
    },
  },
};
</script>
